"use client"
import React, { useEffect } from 'react'
import {FaAdjust, FaArrowLeft} from "react-icons/fa"
import axios from 'axios'
import {CiSettings} from "react-icons/ci"
import { useState } from 'react'
import { getAuth } from "firebase/auth";
import { useRouter } from 'next/navigation'
import {useAuthState} from "react-firebase-hooks/auth"
import  {auth} from '../../firebase'
const About = () => {
  const [currentUser, setCurrentUser] = useState(null);
  const [user] = useAuthState(auth);

  useEffect(() => {
    if (user) {
      setCurrentUser(user.email || "no user logged in");
    } else {
      
    }
  }, [user]);

  const router = useRouter();

  useEffect(() => {
    if (!user) {
      router.push("/signup");
    }
  }, [user, router]);
  const [bg,setBG] = useState("white")
const [isEmpty,setIsEmpty] = useState(false);
const [isRoadMap,setRoadMap] = useState(false)
const [show,setShow] = useState(false)
const [showed,setShowed] = useState(false)
const [value,setValue] = useState(0.5)
const [userinput,setUserInput] = useState("")
const [loading,setLoading] = useState(false)
const [recommendedskills,setRecommendedSkills] = useState("")
const [stepbystep,setStepByStep] = useState("")
const [usefulresources,setUsefulResources] = useState("")
const [roadmapResults,setRoadMapResults] = useState("")

async function Response() {
  try {
    setRoadMap(false)
  const generated_data = await axios.post("../api", {
        topic: userinput
      })
      
  

    console.log(generated_data.data);
    setRoadMapResults(generated_data.data)
    setRecommendedSkills(generated_data.data.recommended_skills)
    setStepByStep(generated_data.data.step_by_step_plan)
    setUsefulResources(generated_data.data.useful_resources)

   alert(roadmapResults.split("")) 

    if (generated_data.data.length === 0) {
      setIsEmpty(true);
      return;
    }
    setRoadMap(true)
    setLoading(true)
    } catch (err) {
    console.error("Error fetching data:", err);
    document.
    setIsEmpty(true);
  }
}


  
  useEffect(() => {
    // Only run on mount
    const first = localStorage.getItem("first");
    if (first) {
      setShowed(true);
      setValue(0);
    } else {
      setValue(4);
    }
  }, []);

  useEffect(() => {
    if (showed) {
      setValue(0);
      localStorage.setItem("first", "true");
    }
  }, [showed]);

  useEffect(() => {
    setBG(localStorage?.getItem("cl") || "white");
  }, []);
 
const options = [
    {name:"nature green",background:"#7C9E86FF",display1:"#C1D89794",display2:"#ffffff99",display3:"#ffffff99"},
   {name:"sky blue",background:"#263c5e",display1:"#ffffffb2",display2:"#ffffff99",display3:"#ffffff99"},
{name:"cozy brown",background:"#898970",display1:"#4170845F",display2:"#C6F4D694",display3:"#D6EBDD9E"},   {name:"18 a'clock",background:"#232225",display1:"#FFFFFFC1",display2:"#FFFFFFC1",display3:"#FFFFFFC1"},{name:"passion red",background:"#654848",display1:"",display2:"#CAB1AFB2",display3:"#fecacab2"},]


  return (
    <div style={{backgroundColor:`${bg}`}} className='flex  justify-center items-center text-black/90 pt-10 min-h-[100vh]  max-h-fit'>

<div className='pt-27 fixed text-amber-50 top-0  opacity-50 hidden'> ( Signed in as :{currentUser} )</div>


{
  !showed && <div onDoubleClick={()=>{setShowed(true)}} className='fixed z-30 border-blue-950/40 ring-4 rounded-xl ring-green-100/70 p-4 border-4  '>
    <h1 className='font-extrabold  bg-gray-200 rounded-xl px-2 opacity-70 font-sans'>to get started choose from the choose from the default templates</h1>
   {
    options.map((option,idx)=>{
  return <div key={idx} className='flex font-serif mb-3 scale-90 p-3 gap-2 items-center'>
        <button id={idx} onClick={()=>{setBG(option.background);localStorage.setItem("1",option.display1);localStorage.setItem("cl",option.background) ;localStorage.setItem("2",option.display2);localStorage.setItem("3",option.display3);}} className='w-10 h-10 rounded-full' style={{backgroundColor:`${option.background}`}}></button>
        <button onClick={()=>{document.getElementById(`${idx}`).click()}}  className='text-blue-900/70 hover:bg-blue-950/30 rounded-2xl px-4 font-serif '>{option.name}</button>
      </div>

    })

  }
  <h1 className='font-extrabold font-sans bg-gray-200 rounded-xl px-2'>double click  color of choice to lock in on choice , click once to check out</h1>
</div>
}

<div className='fixed min-[70rem]:hidden mt-30 w-120 min-h-50 max-h-fit overflow-auto border-4 flex-col animate-l1 flex justify-center border-green-900/50 items-center z-40 opacity-80 text-center'>We noticed (╯°□°）╯︵ ┻━┻ that your window is getting to small ,please resize for optimal study sessions be a study pal wont you(❁´◡`❁), increase to conitnue ,or go to full screen here
<h1 className='bg-blue-300 px-4 rounded-xl border-2 border-green-900/40' onClick={()=>{document.documentElement.requestFullscreen()}}>fullscreen</h1></div>
{show == true  && <h1 style={{filter:`blur(${value}px)`}} className='text-white/70 fixed top-30 right-30 n border border-blue-400/50 opacity-60 rounded-2xl p-1'>only avaliable in study aid</h1>}
<h1 onMouseOver={()=>{setShow(true)}} style={{filter:`blur(${value}px)`}} onMouseLeave={()=>{setShow(false)}} className=' fixed top-30 text-3xl  rounded-[50%] border-2 border-dotted hover:bg-blue-100 bg-blue-50
 text-blue-800/20 right-20 duration-300' ><CiSettings></CiSettings></h1>
<div style={{filter:`blur(${value}px)`}} className='w-270 ml-40 shadow-green-50/20 shadow-xl animate-s min-w-[60rem] max-[80rem]:left-90 max-[90rem]:scale-80 max-[94rem]:scale-85 max-[70rem]:scale-70  border-2 border-blue-300/50 ring-4 ring-blue-800/20 mt-15 bg-black/40 flex justify-center items-center text-blue-200/80 rounded-xl min-h-[70vh] h-fit'> 
{!loading && (
  <h1 className='animate-bounce duration-300 delay-1000 font-sans opacity-70 flex gap-2 pt-2 '>
    <div className='pt-1'><FaArrowLeft /></div>Start a chat
  </h1>
)}

{isRoadMap && (
  <div className='w-full opacity-80 overflow-auto text-blue-700/70 grid h-fit gap-5 grid-cols-1'>
    <div className='bg-white/80 ring-4 flex-col ring-blue-100 shadow-sm border underline-offset-8 decoration-2 flex items-center border-blue-400 p-2 rounded-2xl m-10 h-full'>
      <h1 className='underline font-sans font-extrabold'>Recommended skills to focus on:</h1>
      <pre className='text-black font-bold text-[1.1rem] whitespace-pre-wrap'>{recommendedskills || roadmapResults || "No data returned."}</pre>
    </div>
    <div className='bg-white/80 ring-4 flex-col ring-blue-100 shadow-sm border underline-offset-8 decoration-2 flex items-center border-blue-400 p-2 rounded-2xl m-10 h-full'>
      <h1 className='underline font-sans font-extrabold'>Step by step 3 months plan to get better at it:</h1>
      <pre className='text-black font-bold text-[1.1rem] whitespace-pre-wrap'>{stepbystep || "No data returned."}</pre>
    </div>
    <div className='bg-white/80 ring-4 flex-col gap-5 ring-blue-100 shadow-sm border underline-offset-8 decoration-2 flex items-center border-blue-400 rounded-2xl m-10'>
      <h1 className='underline font-sans font-extrabold'>Recommended resources you can use:</h1>
      <pre className='text-black font-bold text-[1.1rem] whitespace-pre-wrap'>{usefulresources || "No data returned."}</pre>
    </div>
  </div>
)}
</div> 

<div style={{filter:`blur(${value}px)`}} className='fixed left-6 gap-4 flex flex-col'>
  
<div className=''></div>

  <div className='bg-green-200/90 mt-10 scale-90 max-[65rem]:hidden max-[94rem]:scale-78 shadow-green-50 shadow-xl animate-f1 animate-fall flex-col flex w-60 h-50  border-2 border-blue-200/70 ring-4 ring-blue-100 rounded-xl '>
<textarea value={userinput} onChange={(e) => setUserInput(e.target.value)}  className='h-full cursor-pointer p-10 focus:border-white focus:border opacity-60 rounded-xl border-black border font-serif' placeholder='software engineer ,game development ,accountant'></textarea>
<div className='flex-1/8 bg-blue-50 flex justify-center items-center opacity-70'>
<button disabled={loading} onClick={()=>{setIsEmpty(false);Response();setLoading(true)}} className='bg-blue-100 cursor-pointer hover:bg-blue-300/40 p-1 rounded-xl ring-white ring-4 border-blue-800/70 border font-serif'>Create Roadmap</button></div>
</div>

<div className='bg-red-200/70 scale-90 max-[65rem]:hidden shadow-green-50 shadow-xl max-[94rem]:scale-78  animate-f2 flex-col flex w-60 h-50    border-2 border-blue-200/70 ring-4 ring-blue-100 rounded-xl '>
<textarea className='h-full cursor-pointer p-10 focus:border-white focus:border opacity-60 rounded-xl border-black border font-serif' placeholder='how to become a real estate agent...'></textarea>
<div className='flex-1/8 bg-blue-50 flex justify-center items-center opacity-70'>
<button disabled={loading} onClick={()=>{setLoading(true)}} className='bg-blue-100 cursor-pointer hover:bg-blue-300/50 p-1 m-1 rounded-xl ring-white ring-4 border-blue-800/70 border font-serif'>Career Advice</button></div>
</div>

<div className='bg-amber-200/90 shadow-green-50 shadow-xl scale-90 max-[94rem]:scale-78 max-[65rem]:hidden animate-f3 flex-col flex w-60 h-50   border-2 border-blue-200/70 ring-4 ring-blue-100 rounded-xl '>
<textarea className='h-full cursor-pointer p-10 focus:border-white focus:border opacity-60 rounded-xl border-black border' placeholder='what are your thought on...'></textarea>
<div className='flex-1/8 bg-blue-50 flex justify-center items-center opacity-70'>
<button disabled={loading} onClick={()=>{setLoading(true)}} className='bg-blue-100 cursor-pointer hover:bg-green-300/50 m-1 p-1 rounded-xl ring-white ring-4 border-blue-800/70 border font-serif'>General chat</button></div>
</div>

</div>

    </div>
  )
}

export default About